<template>
  <div>
    <h1 class="operation">
      flip: {{flipTimes}} | fail: {{failTimes}}
    </h1>
    <div class="operation">
      <button class="btn-bg" @click="flip">Flip it!</button>
    </div>

    <div class="wall" :class="{rotating: rotating, flipped: flipped}">
      <div v-for="(row, rowIndex) in board">
        <tile v-for="(col, colIndex) in row"
              :colored="col"
              @click.native="answer(rowIndex, colIndex)"></tile>
      </div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import Tile from './Tile.vue'
  import {generateBitMatrix} from 'helpers/matrix'
  import {random} from 'helpers/rnd'

  const wait = (ms) => new Promise(resolve => setTimeout(resolve, ms))

  export default {
    name: 'hello',
    components: { Tile },
    data () {
      return {
        col: 2,
        row: 2,
        factor: 0.9,
        board: [],
        rotating: false,
        flipped: false,
        mutatedPoints: [],

        flipTimes: 0,
        failTimes: 0
      }
    },
    mounted () {
      this.refresh()
    },
    methods: {
      refresh () {
        this.rotating = false
        this.flipped = false
        const { row, col, factor } = this
        this.board = generateBitMatrix({ row, col, factor })
        this.mutatedPoints = [[random(0, row), random(0, col)]]
      },
      async flip () {
        this.flipTimes += 1
        this.rotating = true
        await wait(1100)
        this.mutatedPoints.forEach(([row, col]) => {
          Vue.set(this.board[row], col, Math.abs(this.board[row][col] - 1))
        })
        this.rotating = false
        this.flipped = !this.flipped
      },
      answer (row, col) {
        const index = this.mutatedPoints.findIndex(([r, c]) => r === row && c === col)
        if (index !== -1) {
          window.alert('Success')
          if (this.factor <= 0.6) {
            this.col += 1
            this.row += 1
            this.factor = 0.9
          } else {
            this.factor -= 0.1
          }
          this.refresh()
        } else {
          this.failTimes += 1
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="stylus" scoped rel="stylesheet/stylus">
  .operation
    margin-bottom 20px

  .btn-bg
    width 100px
    height 40px

  .wall
    display inline-block
    background-color aliceblue
    margin-right 25px
    transform-origin right
    transition transform 1s

  .flipped
    transform rotateY(180deg)

  .rotating
    transform rotateY(90deg)
</style>



